.favorites{
    .catalog-item{
        &__fav{
            display: none;
            top: 5px;
            left: auto;
            right: 5px;
            overflow: hidden;
            &:before,
            &--is-active{
                background-position: -83px -260px !important;
                margin-top: 0;
            }
            &:hover{
                &:before{
                    background-position: -109px -260px !important;
                }
            }
        }
        &:hover{
            .catalog-item__img{
                &:after{
                    display: none;
                }
            }

            .catalog-item__button_fav{
                display: block;
            }
        }
    }
}
