
.footer-bonus{

	width: 100%;
	height: 70px;
	position: fixed;
	bottom: 0;
	left: 0;
	z-index: 222;
	background: #f8f8f8;
	border: solid 1px #e7e8e6;
	-webkit-box-shadow: 0 0 2px -2px #000;
	   -moz-box-shadow: 0 0 2px -2px #000;
	        box-shadow: 0 0 2px -2px #000;

	@media @touch{
		display: none;
	}

	&__inner{
		width: 999px;
		margin: 0 auto;
		padding: 15px 180px 0 255px;
		background: url(../img/bonus-reg-card.png) no-repeat 125px 6px;
		background-size: 111px auto;
		position: relative;
	}

	&__title{
		.font-circe_regular(25px, 1, #333);
		text-transform: uppercase;
	}

	&__descr{
		.font-circe_regular(14px, 22px, #333);
		text-transform: uppercase;
	}

	&__getcard{
		display: inline-block;
		border: none;
		background: #ed1a20;
		text-transform: uppercase;
		border-radius: 3px;
		text-decoration: none;
		padding: 0 20px;
		position: absolute;
		top: 15px;
		right: 180px;
		text-align: center;
		.font-circe_regular(11px, 36px, #fff);
		.transition();

		&:hover{
			background: #b32023;
		}
	}

	&__close{
		display: block;
		border: none;
		width: 15px;
		height: 14px;
		position: absolute;
		top: 27px;
		right: 20px;
		cursor: pointer;
		background: url('data:image/png;base64,');

		&:hover{
			background-position: -15px 0;
		}

	}
}
