.filter-group {
	font-size: 10px;
	text-transform: uppercase;
	margin: 0 0 10px;
	padding: 0;
	border: none;
	clear: both;
}
.filter-group__header {
	font-family: 'Circle';
	color: #f3483e;
	margin-bottom: 3px;
}

.filter-group__options_price_line {
	margin-left: 17px;
	margin-bottom: 6px;
}

.filter-group__options_price {
	margin-left: 17px;
	margin-bottom: 6px;
}
.filter-group__option--2-column {
	/* -webkit-column-count: 2;
	    -moz-column-count: 2;
	    column-count: 2; */
    vertical-align: top;

   /*  -webkit-column-gap: 8px;
   -moz-column-gap:8px;
   column-gap: 8px; */
}
.filter-group__option--3-column {
	/* -webkit-column-count: 3;
	    -moz-column-count: 3;
	    column-count: 3; */
}
.filter-group__header--selection {
    float: left;
	margin-right: 9px;
	margin-top: 3px;
}
.filter-group__option {
	display: inline-block;
	padding: 0 20px 0 10px;
	line-height: 20px;
	background: #ffffff;
	border: 1px solid #e8e8e8;
	border-radius: 3px;
	position: relative;
	white-space: nowrap;
	margin: 0 5px 10px 0;
}
.filter-group__option:after {
	position: absolute;
	content: '';
	width: 9px;
	height: 9px;
	right: 6px;
	top: 5px;
	background-image: url('@{img}/filter/filter_option__checkbox.png');
    background-repeat: no-repeat;
    background-position: -34px -36px;
}
.filter-group__option:hover:after {
	background-position: -34px -25px;
}
.filter-group__option:hover {
	border: 1px solid #ed3939;
	cursor: pointer;
}
.filter-group__options_colorinsert{
    position: relative;
}

.filter-group__note-link{
    float: right;   
    font-size: 10px;
    color: #a9a9a9;
    background: url('data:image/png;base64,');
    background-repeat: no-repeat;
    background-position: 0 50%;
    padding-left: 20px;
    position: relative;
    z-index: 2;
    line-height: 14px;
}
.filter-group{
    margin: 0 0 4px;
}

.filter-group__header{
    margin-bottom: 5px;
}

.filter-group__options{
    position: relative;
    left: -1px;
}
.filter-group__option--2-column{
/*     -webkit-column-gap: 29px;
   -moz-column-gap: 29px;
        column-gap: 29px; */
}

.filter-product_diamondquantity-properties .filter-group__option--2-column{
/*     -webkit-column-gap: 0;
   -moz-column-gap: 0;
        column-gap: 0; */
}

.filter-group--weight{
    margin-top: -4px;
}
.filter-product_diamondquantity-properties .filter-group__option--3-column {
/*     -webkit-column-gap: 0;
-moz-column-gap: 0;
column-gap: 0; */
}

.filter-group__option--2-column .filter-option {
	width: 70px;
}
.filter-group__option--3-column .filter-option__title {
	padding-right: 25px;
}










.filter-group__options .js-scroll-pane {
	overflow: auto;
	width: 100%;
}
.filter-section-1 .filter-group__scroll-settings {
	overflow:  hidden
}
.filter-group__options .jspVerticalBar {
	padding-bottom: 3px;
}
.filter-group__options .jspDrag {
	background: #e8e8e8;
	border: 1px solid #d1d1d1;
	border-radius:3px;
	border-bottom: none;
	left: 1px;
	width: 9px;
}
.filter-group__options .jspDrag:before {
	position: absolute;
	content: '';
	width: 100%;
	height: 8px;
	left:0;
	bottom:0;
	border-radius:3px;
	border-bottom: 1px solid #e8e8e8;
}
.filter-group__options .jspDrag:after {
	position: absolute;
	content: '';
	width: 100%;
	height: 8px;
	left:0;
	top:0;
	border-radius:3px;
	border-bottom: 1px solid #d1d1d1;
}
.filter-group__options .jspTrack {
	z-index:1;
	background: #f5f5f5;
}
.filter-group__options .jspTrack:after {
	position: absolute;
	content: '';
	width: 4px;
	background: #fbfbfb;
	border: 1px solid #e8e8e8;
	left:0;
	top: 0;
	height: 100%;
	left: 3px;
	z-index:-1;
	border-radius: 4px;
}
.filter-group__options .jspVerticalBar {
	width: 9px;
	right: 1px;
}
.filter-group__options .jspVerticalBar,
.filter-group__options .jspHorizontalBar {
	background: none;
}
.filter-group__option_custom {
	display: inline-block;
    padding: 0 20px 0 10px;
    line-height: 20px;    
    border: 1px solid #e8e8e8;
    border-radius: 3px;
    position: relative;
    white-space: nowrap;
    margin: 0 5px 10px 0;
}

.filter-product_diamondquantity-properties .filter-group{
    margin: 0 0 9px;
}

@media only screen and (min-width : 1024px) {

	#filter-selected-params{
		float: left;
		height: auto;
		max-width: 565px;
		min-height: 40px;
		max-height: 40px;
		white-space: nowrap;

		.filter-group__header{
			float: left;
		}

		.jspHorizontalBar{
			bottom: 5px;
			height: 2px;

			.jspTrack{
				background: #d6d6d6 !important;
			}

			.jspTrack::after{
				display: none;
			}

			.jspDrag{
				-webkit-border-radius: 0;
				   -moz-border-radius: 0;
				        border-radius: 0;
				border: none;
				background: #c4c4c4;
			}

			.jspDrag::before,
			.jspDrag::after{
				border: none;
			}
		}
	}

	#change-filter-link{
		display: block;
		position: absolute;
		right: 15px;
		bottom: 8px;

		span{
			display: block;
			position: relative;
			z-index: 2;
			border-left: solid 1px #ababaa;
			padding: 14px 0 14px 18px;
			background: #f5f5f5;
		}

		&:before{
			content:'';
			position: absolute;
			top: 50%;
			left: 0;
			-webkit-transform: translateY(-50%);
			   -moz-transform: translateY(-50%);
			    -ms-transform: translateY(-50%);
			     -o-transform: translateY(-50%);
			        transform: translateY(-50%);
			z-index: 1;
			width: 30px;
			height: 75%;
			-webkit-box-shadow: 0 0 7px -1px #000;
			   -moz-box-shadow: 0 0 7px -1px #000;
			        box-shadow: 0 0 7px -1px #000;
			-webkit-border-radius: 50%;
			   -moz-border-radius: 50%;
			        border-radius: 50%;
		}
	}

}
